/**
 * @description: 绑定事件
 * @param :
 * @return {*}
 */
export const bindEvent = function (obj, ev, fn) {
    obj.addEventListener ? obj.addEventListener(ev, fn, false) : obj.attachEvent('on' + ev, fn)
}

/**
 * @description: 解绑事件
 * @param :
 * @return {*}
 */
export const unbindEvent = function (obj, ev, fn) {
    obj.removeEventListener ? obj.removeEventListener(ev, fn, false) : obj.detachEvent('on' + ev, fn)
}

/**
 * @description: 底部是否可见
 * @param :
 * @return {*}
 */
export const bottomVisible = function () {
    return document.documentElement.clientHeight + window.scrollY >= (document.documentElement.scrollHeight || document.documentElement.clientHeight)
}

/**
 * @description: 顺滑的滚动到顶部
 * @param :
 * @return {*}
 */
export const scrollToTop = function () {
    const c = document.documentElement.scrollTop || document.body.scrollTop
    if (c > 0) {
        window.requestAnimationFrame(scrollToTop)
        window.scrollTo(0, c - c / 8)
    }
}
/**
 * @description: 顺滑的滚动到当前元素
 * @param {Element} element
 */
export const smoothScroll = function (element) {
    return document.querySelector(element).scrollIntoView({ behavior: 'smooth' })
}
/**
 * @description: 获取元素的滚动位置坐标
 * @param {Elemnt} el
 */
export const getScrollPosition = function (el = window) {
    return {
        x: el.pageXOffset !== undefined ? el.pageXOffset : el.scrollLeft,
        y: el.pageYOffset !== undefined ? el.pageYOffset : el.scrollTop
    }
}
/**
 * @description:防抖
 * @param { Function } func : 防抖的事件响应函数
 * @param { Number } wait ：事件响应函数执行需求的单位时间
 * @param { Boolean } immediate ：是否立即执行
 * @return 内部函数执行结果（返回的对象身上可以调用取消功能）
 */
export const debounce = function (func, wait, immediate) {
    let timeout
    let debounced = function () {
        let arg = arguments
        let context = this
        if (timeout) clearTimeout(timeout)
        if (immediate) {
            let callNow = !timeout
            timeout = setTimeout(function () {
                timeout = null
            }, wait)
            if (callNow) func.apply(context, arg)
        } else {
            timeout = setTimeout(function () {
                func.apply(context, arg)
            }, wait)
        }
    }
    debounced.cancel = function () {
        clearTimeout(timeout)
        timeout = null
    }
    return debounced
}

/**
 * @description:节流
 * @param { Function } func : 节流的事件响应函数
 * @param { Number } wait ：事件响应函数执行需求的频率时间
 * @param { Object } options ：配置对象，包含两个值 immediate（是否立即执行）和 trailing（最后是否还执行一次）
 */
export const throttle = function (func, wait, options) {
    let timeout, context, args
    let old = 0 //之前的时间
    if (!options) options = {}
    return function () {
        context = this
        args = arguments
        // 获取当前的时间戳
        let now = new Date().valueOf()
        if (options.leading === false && !old) {
            old = now
        }
        if (now - old > wait) {
            // 第一次会立即执行
            if (timeout) {
                clearTimeout(timeout)
                timeout = null
            }
            func.apply(context, args)
            old = now
        } else if (!timeout && options.trailing !== false) {
            // 最后一次也会执行
            timeout = setTimeout(() => {
                old = new Date().valueOf()
                timeout = null
                func.apply(context, args)
            }, wait)
        }
    }
}

/**
 * 返回指定元素的生效样式
 * @param {Element} el
 * @param {String} attr
 */
export const getStyle = function (el, attr) {
    el = typeof el === 'string' ? document.querySelector(el) : el
    if (!el) return
    if (attr == 'left') {
        return el.offsetLeft
    } else if (attr == 'top') {
        return el.offsetTop
    } else if (attr == 'width') {
        return el.offsetWidth
    } else if (attr == 'height') {
        return el.offsetHeight
    } else {
        return el.currentStyle ? el.currentStyle[attr] : getComputedStyle(el)[attr]
    }
}

/**
 * 隐藏所有的指定标签
 * @param  {...any} el
 */
export const hide = function (...el) {
    ;[...el].forEach(e => (e.style.display = 'none'))
}

/**
 * 随机十六进制颜色
 */
export const randomHexColorCode = function () {
    let n = (Math.random() * 0xfffff * 1000000).toString(16)
    return '#' + n.slice(0, 6)
}

/**
 * 获取不同类型变量的字节长度
 * @param {any} val
 */
export const size = function (val) {
    return Array.isArray(val) ? val.length : val && typeof val === 'object' ? val.size || val.length || Object.keys(val).length : typeof val === 'string' ? new Blob([val]).size : 0
}
/**
 * @description:
 * @param :
 * @return {*}
 */
export const bytesToSize = function (bytes) {
    if (!bytes) return ''
    if (bytes === 0) return '0 B'
    let k = 1024
    let sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
    let i = Math.floor(Math.log(bytes) / Math.log(k))
    let num = bytes / Math.pow(k, i)
    return num.toFixed(2) + ' ' + sizes[i]
}
/**
 *  JSON 转 fromData
 *  @param { Object } o 需要转成formData 的对象
 */
export const jsonToFromData = function (o) {
    let ret = Object.keys(o).reduce((p, c) => !p.append(c, o[c]) && p, new FormData())
    ret.check = () => o
    return ret
}

/**
 * 主动触发DOM身上存在的方法
 * @param {Element} Node
 * @param {String} EventType
 */
export const trigger = function (Node, EventType) {
    if (EventType in Node) Node[EventType]()
}

/**
 * 主动触发自定义方法
 * @param {Element} node
 * @param {String} type
 */
export const fireEvent = function (node, type) {
    let event = document.createEvent('Event')
    event.initEvent(type, true, true)
    node.dispatchEvent(event)
}

/**
 * 回到顶部
 * 回到顶部元素的Id
 * @param {String} btnId
 */
export const backTop = function (btnId, offset = 100) {
    let btn = document.getElementById(btnId)
    let d = document.documentElement
    let b = document.body
    window.onscroll = set
    btn.style.display = 'none'
    btn.onclick = function () {
        btn.style.display = 'none'
        window.onscroll = null
        this.timer = setInterval(function () {
            d.scrollTop -= Math.ceil((d.scrollTop + b.scrollTop) * 0.1)
            b.scrollTop -= Math.ceil((d.scrollTop + b.scrollTop) * 0.1)
            if (d.scrollTop + b.scrollTop == 0) clearInterval(btn.timer, (window.onscroll = set))
        }, 10)
    }
    function set() {
        btn.style.display = d.scrollTop + b.scrollTop > offset ? 'block' : 'none'
    }
}

/**
 * @description: 缓冲动画：left => 从左向右作缓冲动画运动 top => 从上向下作缓冲动画运动 负值可以达到4个方向的缓冲动画
 * @param :
 * @return {*}
 */
export const bufferAnimation = function (el, target, direction = 'left') {
    el = typeof el === 'string' ? document.querySelector(el) : el
    if (!el) return
    let offset = direction === 'left' ? 'offsetLeft' : 'offsetTop'
    //清除历史定时器
    clearInterval(el.timer)
    //开启定时器
    el.timer = setInterval(function () {
        //获取步长 确定移动方向(正负值) 步长应该是越来越小的，缓动的算法。
        var step = Math.ceil((target - el[offset]) / 10)
        //到达目标位置
        if (el[offset] == target) {
            //清除定时器
            clearInterval(el.timer)
        } else {
            el.style[direction === 'left' ? 'left' : 'top'] = el[offset] + step + 'px'
        }
    }, 30)
}

/**
 * @description: 匀速动画：
 * @param :
 * @return {*}
 */
export const constantAnimation = function (el, target, direction = 'left') {
    el = typeof el === 'string' ? document.querySelector(el) : el
    if (!el) return
    let offset = direction === 'left' ? 'offsetLeft' : 'offsetTop'
    //清除历史定时器
    clearInterval(el.timer)
    //匀速度值
    var speed = target > el[offset] ? 5 : -5
    //开启定时器
    el.timer = setInterval(function () {
        //到达目标位置
        if (el[offset] == target) {
            //清除定时器
            clearInterval(el.timer)
        } else {
            el.style[direction] = el[offset] + speed + 'px'
        }
    }, 30)
}

/**
 * 动态添加css文件
 * @param {String} cssName : css 名称 不带后缀
 * @param {String} prefix  ：css 前缀
 */
export const dynamicAddCss = function (cssName, prefix = '') {
    let link = document.createElement('link')
    link.setAttribute('href', prefix + cssName + '.css')
    link.setAttribute('type', 'text/css')
    document.getElementsByTagName('head')[0].appendChild(link)
}

/**
 * 动态添加 js文件
 * @param {Array} scriptUrlArr : 需求动态添加的脚本URL数组
 * @param {Function} callback : 回调函数
 */
export const dynamicAddScript = function (scriptUrlArr, callback) {
    scriptUrlArr.forEach(scriptUrl => {
        let script = document.createElement('script')
        script.setAttribute('src', scriptUrl)
        document.body.appendChild(script)
    })
    window.onload = function () {
        callback && callback()
    }
}

/**
 * @description: 判断点击 dom 范围是否是 指定class 或是 class dom下面的孩子dom
 * @param :
 * @return {*}
 */
export const contains = function (selector, subSelector) {
    return document.querySelector(selector).contains(document.querySelector(subSelector))
}

/**
 * @description: 点击 指定 class dom，只要点击的是 class dom，或是 class dom下面的孩子元素，都返回该 class dom
 * @param :
 * @return {*}
 */
export const clickTarget = function (target, targetClass, cb) {
    let ret = null
    if (target.classList && target.classList.contains(targetClass)) {
        ret = target
    } else {
        while (target && target.classList && !target.classList.contains(targetClass)) {
            target = target.parentNode
            if (!target.classList) {
                cb && cb()
                return
            }
        }
        ret = target
    }
    cb && cb(ret)
}

/**
 * @description: 获取网页被卷去的位置
 * @param :
 * @return {*}
 */
export const getScrollXY = function () {
    return document.body.scrollTop
        ? {
              x: document.body.scrollLeft,
              y: document.body.scrollTop
          }
        : {
              x: document.documentElement.scrollLeft,
              y: document.documentElement.scrollTop
          }
}
/**
 * 获取HTML中DOM的最高层级z-index函数
 */
export const getMaxZIndex = function () {
    // 获取HTML中所有的DOM
    let divs = document.all || document.querySelectorAll('*')
    return Math.max(...[...divs].map(i => parseInt(getComputedStyle(i).zIndex) || 1))
}

/**
 * @description: 运动框架
 * @param :
 * @return {*}
 * @param {*} obj
 * @param {*} attr
 * @param {*} target
 * @param {*} time
 * @param {*} back
 */
export const move = function (obj, attr, target, time, back) {
    var startTime = new Date()
    var startVal = parseInt(getStyle(obj, attr))
    var timer = setInterval(function () {
        var nowTime = new Date()
        var moveVal = parseInt(target) - startVal
        var prop = (nowTime - startTime) / time
        if (prop >= 1) {
            prop = 1
            clearInterval(timer)
            if (PicIndex == PicLi.length - 1) {
                PicIndexLast = 1
                back && back()
                objNum++
                if (objNum == 5) {
                    PicIndex = 1
                    objNum = 0
                }
                prop = NaN
            } else if (PicIndex == 0) {
                PicIndexLast = PicLi.length - 2
                back && back()
                objNum++
                if (objNum == 5) {
                    PicIndex = PicLi.length - 2
                    objNum = 0
                }
                prop = NaN
            }
        }
        obj.style[attr] = prop * moveVal + startVal + 'px'
    }, 20)
}

/**
 * @description: 封装方法-input框只能输入数字 输入框光标可以左右移动 可以backspace
 * @param :
 * @return {*}
 * @param {Element} ele
 */
export const onlyNumber = function (ele) {
    if (ele.length === undefined) {
        ele.onkeydown = function (ev) {
            var oEevent = ev || event
            if (oEevent.keyCode != 8 && oEevent.keyCode != 37 && oEevent.keyCode != 39 && (oEevent.keyCode < 48 || oEevent.keyCode > 57)) return false
        }
    } else {
        for (var i = 0; i < ele.length; i++) {
            ele[i].onkeydown = function (ev) {
                var oEevent = ev || event
                if (oEevent.keyCode != 8 && oEevent.keyCode != 37 && oEevent.keyCode != 39 && (oEevent.keyCode < 48 || oEevent.keyCode > 57)) return false
            }
        }
    }
}

/**
 * @description: 复制
 * @param :
 * @return {*}
 */
export const copy = function (selector, cb) {
    typeof selector === 'string' ? document.querySelector(selector).select() : selector.select()
    document.execCommand('Copy')
    cb && cb()
}
